@use "sass:math";

// Quotation
// TODO: Make almost completely 'generic'
// Consider image / quote / etc. `figures`
.c-quotation {

  $this: #{&};

  align-items: center;
  // background-color: red;
  display: flex;
  flex-direction: column;
  gap: 35px; // #TODO: arrow (var) + space
 
  @include bp-m {
    align-items: flex-end;
    flex-direction: row; // #i18n -friendly?
  }

  blockquote {
    font-size: $fs-25;
    font-weight: 300;
    hanging-punctuation: first; // Support rubbish as time of writing
    margin: 0; // Let `gap` do this
    
    @supports not (hanging-punctuation: first) {
      text-indent: -0.25em;
    }

    @include bp-m {
      // font-size: $fs-35;
      // font-weight: 200;
    }

    > * {
      margin: 0;

      & + * {
        margin-top: 1em;
      }
    }

    p {
      // quotemarks
      &:before { content: "\0201C"; }

      &:after { content: "\0201D"; }
    }
  }
  
  // Only use for positioning for now - style is from `entity`
  figcaption {
    flex: 1 0 25%;
  }

  // Variants
  // Size?
  // Orientation?

  // Flipped orientation
  @include bp-m {
    &--flip {
      flex-direction: row-reverse;

      // Also requires triangle to be flipped below
    }
  }

  // Branded background
  &--background {
    blockquote {
      background-color: $clr-b2; // #004f75
      border-radius: .75em;
      color: white;
      padding: $vmargin;
      position: relative; // contain those arrows

      // Triangle
      &:after {
        content: "";
        border: 10px solid;
        border-color: $clr-b2
          transparent
          transparent
          $clr-b2;
        left: 50%;
        position: absolute;
        top: 100%;

        @include bp-m {
          left: 100%;
          top: 50%;
        }
      }
    }

    @include bp-m {
      &#{$this}--flip {
        blockquote::after {
          border-right-color: $clr-b2;
          border-left-color: transparent;
          left: auto;
          right: 100%;
        }
      }
    }
  }

}


// Entity / aka Avatar
// TODO: Genericize
.c-entity {

  display: flex;
  flex-direction: row;
  gap: math.div($spacing, 2);
  justify-content: center;
  
  // TODO: I don't like this. Should break appropriately without MQ
  @include bp-s-less {
    flex-wrap: wrap;
  }

  > * {
    // flex: 0 0 auto;
  }

  // TODO: Shared breakpoint
  // TODO: Container queries?
  @include bp-m {
    flex-direction: column;
  }

  &__text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: .25em;
    line-height: 1.25;
  }

    &__name {
      color: $clr-b0;
      font-size: $fs-22;
      font-weight: 700;
    }

    &__role {
      
    }

  &__avatar {
    aspect-ratio: 1 / 1; // TODO: Safari testing
    border-radius: 100%;
    align-self: stretch;
    order: -1; // default to avatar-first
    // TODO: For `picture` element / responsive images?
    // overflow: hidden;
    width: 75px;

    @include bp-m {
      width: 125px;
    }
  }

}


// Buttons
// thnx https://piccalil.li/blog/how-i-build-a-button-component/
.c-button {

  @mixin textRules() {
    font-family: $ff-1;
    font-size: var(--button-font-size, #{$fs-24});
    font-style: normal;
    font-weight: 700;
    line-height: 1.1;
  }

  @include textRules();

  align-items: center; // centers icons
	background-color: var(--button-bg, #{$clr-b5});
	border: none;
	border-radius: 0.4rem; // 
	color: var(--button-color, #{$clr-b2}) !important;
  cursor: pointer;
  // TODO: Re-work how icons are used
	display: inline-flex; // keep iconography centered
  flex: 0 0 auto;
  flex: 0 1 auto; // changed to allow wraping in mobile 
	gap: 0.35em; // For icons, if present
	justify-content: space-between;
	justify-self: flex-start; // stops growing when used in a grid
  line-height: 1.1;
  padding: var(--button-padding, 0.8em 1.25em); // 
  text-align: center;
	text-decoration: none;
	text-transform: none;
  transition:
    box-shadow 0.1s,
    background-color 0.1s,
    color 0.1s;
	user-select: none;
	z-index: 1;

  text-align: center;

  &:focus,
  &:hover {
    background-color: var(--button-hover-bg, #{lighten($clr-b5,7%)});
    color: $clr-b2;
  }

  &:active {
    background: var(--button-active-bg, #{darken($clr-b5,7%)});
    box-shadow: inset 0 0 2px 2px rgba(black,0.2);
  }

  // When `<strong>` element present, consider 'talky' button
  &:has(strong) {
    font-size: $fs-18;
    font-weight: 400;
    // TODO: Expland on this?
    // Consider not to be an icon
    flex-direction: column;

    strong {
      @include textRules();
    }
  }

  // Variations
  // TODO: Crap defaults: won't always be on a dark BG
  &--naked {
    --button-bg: transparent;
    // --button-color: #fff;
    --button-hover-bg: transparent;
    // --button-hover-color: #{darken(#fff, 10)};
    --button-active-bg: transparent;
    // --button-active-color: #{darken(#fff, 10)};
    --button-font-size: #{$fs-18};

    text-decoration: underline;
    text-decoration-color: var(--button-decoration-color, currentColor);

    // TODO: Remove - shouldn't be necessary
    &:focus,
    &:hover {
      text-decoration: underline;
    }
  }

  &--accent {
    --button-bg: #{$clr-b1};
    --button-color: #fff;
    --button-hover-bg: #{$clr-b1a};
  }

}


// CTA banner / block
// TODO: Uses lots of other 'bits' for placement - so fragile
.c-cta-banner {

  .c-button--naked {
    --button-color: #fff;
    --button-hover-bg: transparent;
    --button-hover-color: #{darken(#fff, 10)};
    --button-active-bg: transparent;
    --button-active-color: #{darken(#fff, 10)};
    --button-decoration-color: #{rgba(#fff, .5)};
  }

  font-size: $fs-25;
  text-wrap: balance;
  max-width: 820px; // 

  &__emphasis {
    font-size: $fs-35;
    font-weight: 300;

    strong {
      font-weight: 800;
    }
  }

  &__actions {
    align-items: center;
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
  }

}

