// background themes

// dark blue to orange pattern - homepage, about + medical affairs training

.bg-1 { 
  background-color: $clr-b3;
  background-image:
    url(../../assets/images/content/patterns/landscape.svg),
    linear-gradient(to bottom, $clr-b2 0%, $clr-b3 100%);
  background-size: 99px auto, cover;
  background-repeat: repeat;
  background-position: 0 0;
  color: white;
  box-shadow: inset 0 -3px 17px rgba(black,0.3);
}

// light blue to white pattern - main page-intro background

.bg-2 { 
  background-color: rgba($clr-b5,0.9);
  background-image:
    linear-gradient(to bottom, transparent 0%, white 100%),
    url(../../assets/images/content/patterns/landscape-dark.svg);
  background-size: cover, 99px auto;
  background-repeat: repeat;
  background-position: 0 0;
}

// dark blue pattern - power stats + product boxes

.bg-3 {
  background-color: $clr-b2;
  background-image: url(../../assets/images/content/patterns/landscape.svg);
  background-size: 99px auto, cover;
  background-repeat: repeat;
  background-position: 0 0;
  color: white;
}

// white to light blue gradient - services + resources blocks

.bg-4 {
  background-image: linear-gradient(to bottom, white 0%, $clr-3 40%);
}

// light grey pattern - FAQs, other alternativing sections

.bg-5 {
  background-color: $clr-4;
  background-image: url(../../assets/images/content/patterns/landscape-dark.svg);
  background-size: 99px auto;
  background-repeat: repeat;
  background-position: 0 0;
}

// light blue to white pattern - guide only

.bg-guide {
  background-color: rgba($clr-b5,0.9);
  background-image:
    linear-gradient(to bottom, transparent 0%, white 500px),
    url(../../assets/images/content/patterns/landscape-dark.svg);
  background-size: cover, 99px auto;
  background-repeat: repeat;
  background-position: 0 0;
}

// strip

aside.strip,
div.strip {
  position: relative;
  padding: calc($spacing*1.5) 0;
  @include bp-m {padding: calc($spacing*2) 0;}

  &.section {
    padding: calc($spacing*2) 0 calc($spacing*1);

    @include bp-m {
      padding: calc($spacing*3) 0 calc($spacing*2);
      &.latest p.lead span {display: block;}
    }

    div.wrap > h2 {@extend .h1;}
  }

  &.core {@include bp-m-less {padding: 0;}}
  &.alt {padding-top: 0;}

  // contact 

  &.contact {
    @include bp-m {text-align: left;}
  }

  // team

  &.team {
    h3 {@extend .h2;}
  }
}

// newsletter

div.newsletter {
  text-align: center;

  &.bg-3,
  &.bg-5 {
    overflow: hidden;
    margin-bottom: $vmargin;
    border-radius: $border-radius;
    padding: calc($spacing*1.5) $spacing;

    ul.buttons,
    > p:last-child {margin-bottom: 0;}
  }

  p.lead {
    margin-bottom: 0.75rem;
    text-wrap: balance;
  }
}

// page intro

div.page-intro {
  position: relative;
  z-index: 2;
  padding: calc($spacing*4) 0 calc($spacing*2);
  text-align: center;

  @include bp-m {padding: calc($spacing*6) 0 calc($spacing*2.5);}

  body.analytics & {display: none;}

  body.home &,
  &.consult-cta {@include bp-m {padding-bottom: calc($spacing*4)}}

  &.consult-cta {
    text-align: center;

    body.landing & {
      position: relative;
      background: #EAEDF2;

      @include bp-l {
        div.wrap:after {
          position: absolute;
          top: 0; bottom: 0; right: 0;
          width: 40%;
          background-repeat: no-repeat;
          background-position: 0 50%;
          background-image: url(../../assets/images/content/page-intro/landing-woman.jpg);
          background-size: cover;
          content: "";
        }
      }
    }

    body.services & {
      div.wrap {
        position: relative;

        @include bp-l {

          &:after {
            position: absolute;
            bottom: -2rem; right: 1rem;
            width: 32%;
            min-height: 550px;
            background-repeat: no-repeat;
            background-position: 50% 100%;
            background-size: 100% auto;
            content: "";
          }

          &.mdr-consulting:after {background-image: url(../../assets/images/content/page-intro/mdr-consulting.png);}
          &.ivdr-consulting:after {background-image: url(../../assets/images/content/page-intro/ivdr-consulting.png);}
          &.clinical-evaluation:after {
            background-image: url(../../assets/images/content/page-intro/clinical-evaluation.png);
            bottom: -4rem;
          }

          &.monthly .content {
            margin: auto;
          }
        }
      }
    }

    div.content {
      margin: 0 auto;
      @include bp-l {
        margin: 0;
        width: 60%;
      }

      div.inner {
        margin: 0 auto;
        max-width: 600px;

        > :last-child {margin-bottom: 0;}
      }
    }

    // Monthly offer service page
    & .monthly {
      & .content {
        @include bp-l {
          width: 80%;

          & .inner {
            max-width: 80%; // 

            .general-wrap {
              margin-top: 2rem;
              margin-bottom: 2rem;
            }
          }
        }
      }
    }

    h1 {
      font-size: $fs-30;
      text-wrap: balance;

      @include bp-s {font-size: $fs-40;}
      @include bp-m {font-size: $fs-45;}
      @include bp-l {font-size: $fs-50;}
    }

    p.lead {
      margin: 0 0 $vmargin;
      text-wrap: balance;
    }
  }

  body.form-response & {
    @include bp-m {padding: calc($vmargin*5) 0 calc($vmargin*3);}
    @include bp-l {padding: calc($vmargin*6) 0 calc($vmargin*4);}
  }

  body.article & {
    padding: calc($spacing*4) 0 0;
    @include bp-m {padding-top: calc($vmargin*5);}

    div.wrap {
      position: relative;
      z-index: 2;
    }

    time {
      font-size: $fs-15;
      //color: lighten($clr-1, 20%);
    }

    h1 {
      margin: calc($vmargin*0.5) auto $vmargin;
      max-width: $article-width;
      text-wrap: balance;
    }

    div.author-info {
      flex: 1 0 auto;
      margin-bottom: 0.5rem;

      @include bp-m {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: $vmargin 0;
      }

      a.author-linkedin,
      span.team-photo {
        flex: 0 1 auto;
        display: block;
        margin: 0 auto 0.5rem;
        font-size: $fs-20;

        @include bp-m {margin: 0;}
      }

      ul.general {
        list-style: none;
        font-size: $fs-16;

        @include bp-m {
          margin: 0 0.75rem;
          text-align: left;
        }

        li a {font-weight: 700;}

        li[class*="icon-"] {
          display: flex;
          align-items: center;
          justify-content: center;

          @include bp-m {justify-content: left;}

          &:before {margin-right: 0.25rem;}

          // a {
          //   text-decoration: none;
          //   color: $clr-1;

          //   @include bp-m {
          //     &:hover,
          //     &:focus {color: $clr-b1;}
          //   }
          // }
        }
      }
    }
  }

  &.product-summary {
    @include bp-m {
      padding: calc($spacing*6.5) 0 calc($spacing*2.5);
    }
  }

  div.wrap {
    > div.row {align-items: center;}
    > :last-child {margin-bottom: 0;}
  }

  figure {margin: $vmargin auto;}

  // overview - left side

  div.overview {
    @include bp-m {
      text-align: left;
      
      > :last-child {margin-bottom: 0;}
    }

    h1 {
      body.toolkit &,
      body.services & {
        font-size: $fs-34;
        @include bp-m {
          font-size: $fs-50;
          &.small {font-size: $fs-45;}
        }
      }
    }

    p.lead {
      //margin-bottom: $vmargin;
      max-width: none;
      span {display: inline;}

      @include bp-l {
        span {display: inline-block;}
      }
    }

    p.cta {
      > span {
        display: flex;
        align-items: center;
        margin: 1rem 0 0 2.75rem;
        font-size: $fs-17;

        @include bp-m-less {@include hide-screen();}

        @include bp-m {
          display: inline-flex;
          margin: 1rem;
        }

        &:before {
          order: 2;
          margin-left: 0.25rem;
          font-size: $fs-20;
          color: $clr-b1;
        }
      }
    }

    p.cta-landing {
      > span {
        display: block;
        margin: 0.75rem 0 0 0;
        font-size: $fs-17;
      }
    }
  }

  // circle images - right side

  div.image-space {
    figure {
      overflow: hidden;
      position: relative;
      margin: 0 auto;
      border-radius: 50%;
      max-width: 250px;
      box-shadow: 0 0 50px 10px rgba(black,0.25);

      @include bp-m {margin-bottom: -7rem;}

      body.toolkit & {
        @include bp-m {
          max-width: 450px;
          margin-bottom: -6rem;
        }
      }

      &:after {
        position: absolute;
        top: 0; left: 0; bottom: 0; right: 0;
        z-index: 2;
        border-radius: 50%;
        content: "";
        box-shadow: inset 0 0 0 4px white, inset 0 0 25px 15px rgba(black,0.30);

        @include bp-m {box-shadow: inset 0 0 0 8px white, inset 0 0 25px 15px rgba(black,0.30);}
      }

      img {
        position: relative;
        z-index: 1;
        border: 1px solid white;
        border-radius: 50%;
      }
    }
  }

  // lead capture - right side - image + email + phone

  div.lead-capture {
    position: relative;

    div.cta-box {
      @include bp-m-less {
        background: rgba(black,0.2);
        color: white;
        box-shadow: none;
      }

      @include bp-m {
        margin-bottom: -6rem;
        body.toolkit & {margin-bottom: 0;}
        body.contact &,
        body.associates &,
        body.landing & {margin: 0;}
      }

      figure {
        margin: 0;

        img {
          display: block;
          width: 100%;
          height: auto;
        }
      }

      p {
        margin: 0 0 $vmargin;
        font-size: $fs-20;
        text-align: center;
       
        b {
          display: block;
          font-size: $fs-24;
        }
      }

      &.careers {
        text-align: left;
        font-size: $fs-17;
      
        @include bp-m {
          border-left: 30px solid $clr-warning;
          background: $clr-4;
        }

        dl {
          dt {margin-top: 0.5rem; font-size: $fs-17;}
          dd {padding-left: 1rem;}
        }
      }
    }

    div.video-wrap {margin-bottom: 0;}
  }

  // price summary - right side - EnableCE

  div.price-summary {
    div.cta-box {
      overflow: hidden;
      //@include bp-m {margin-bottom: -8rem;}
    }
  }

  // CTA box - right side 

  div.cta-box {
    position: relative;
    z-index: 4;
    overflow: hidden;
    border-radius: $border-radius;
    background: $clr-4;
    color: $clr-1;
    box-shadow: 0 0 50px 10px rgba(black,0.25);

    body.landing & {background: none;}

    p.header {
      margin: 0;
      padding: 0.5rem;
      background: $clr-warning;
      font-size: $fs-16;

      @include bp-m {
        b {display: inline-block;}
      }
    }

    h3.packages {
      margin: 0;
      padding: 0.4rem 0.5rem;
      background: $clr-3;
      font-size: $fs-16;
      font-weight: 400;
    }

    div.content {
      padding: $spacing;

      &.packages {
        padding: calc($spacing/2) $spacing;
        background: $clr-4;
      }

      > :last-child {margin-bottom: 0;}

      h2 {
        margin-bottom: 0.75rem;
        font-size: $fs-20;
        font-weight: 400;     
      }

      .asterisk {
        display: inline-block;
        text-align: left;
        font-size: $fs-16;

        li {
          margin: 0.2rem 0;
          &:before {font-size: $fs-14;}

          > span {
            display: inline-block;
            vertical-align: baseline;
          }

          span.price {
            font-weight: 700;
            color: $clr-b1;

            &.separate {
              position: relative;
              opacity: 0.6;
              color: $clr-1;

              &:after {
                position: absolute;
                left: 0; right: 0; top: 50%;
                height: 1px;
                background: $clr-b1;
                transform: rotate(-10deg);
                content: "";
              }
            }
          }

          span.frequency {font-size: $fs-14;}
        }
      }

      p.continue {
        margin-top: -0.75rem;
        font-size: $fs-16;
      }
    }

    div.general-wrap {margin: 0;}
  }

  //???????????????????? needs tidying

  h1 {
    //margin-bottom: 0;

    body.about & {
      @include bp-l {font-size: $fs-70;}
    }

    body.home & {
      @include bp-l {font-size: $fs-70;}

      span.alt {
        display: block;
        margin-top: $vmargin;
        font-size: $fs-24;
        @include bp-m {font-size: $fs-27;}
        @include bp-l {font-size: $fs-35;}
      }
    }

    em {
      color: $clr-b2;
      font-style: normal;
    }

    body.error & {
      @extend .h2;
      span {
        display: block;
        font-size: $fs-70;
        @include bp-m {font-size: $fs-100;}
      }
    }

    &.toolkit {
      span.heading {
        display: block;

        @include bp-l {
          > span {display: block;}
        }
        
        body.toolkit-home & {
          font-size: $fs-45;
          @include bp-m {font-size: $fs-75;}
          @include bp-l {font-size: $fs-100;}

          b {font-weight: 400;}

          sup {
            font-weight: 400;
            font-size: $fs-18;
            line-height: 1.0;
            vertical-align: top;

            @include bp-m {line-height: 1.5; font-size: $fs-20;}
            @include bp-l {line-height: 2.0; font-size: $fs-22;}
          }
        }
      }
    }
  }

  ul.checked {
    //margin: calc($vmargin*1.5);
    font-size: $fs-25;
    font-weight: 300;
  }

  p {

    &.lead {
      margin-top: $vmargin;

      @include bp-m {
        margin-left: auto;
        margin-right: auto;
        max-width: 860px;
      }

      body.home & {
        //margin-bottom: 0;
        // @include bp-m {margin-bottom: calc($vmargin*2);}
      }

      body.toolkit-home &,
      body.home & {
        @include bp-m {font-size: $fs-35;}
        //@include bp-l {font-size: $fs-40;}
      }

      span {@include bp-m {display: block;}}
    }
  }
      
  ul.buttons {
    margin: $vmargin auto 0;
  }
}

