aside.strip.cta,
div.strip.cta {
  position: relative;
  z-index: 50;
  padding: calc($spacing*1.5) 0;
  background-color: $clr-b2; 
  //box-shadow: inset 0 -2px 5px rgba(black,0.05);
  text-align: center;
  color: white;

  @include bp-m {
    text-align: left;
    //&.download {padding: $spacing 0;}
  }

  &.client-feedback {
    padding: calc($spacing*2) 0;

    div.wrap {
      //display: block;

      > h2 {
        @include hide-screen;
        width: 0;
        // position: relative;
        // z-index: 1;
        // margin-bottom: $vmargin;
        // font-size: $fs-30;

        // @include bp-m {
        //   width: $col-5;
        //   margin: 0 0 -2rem;
        // }

        // a {
        //   text-decoration-line: none;

        //   @include bp-m {
        //     &:hover,
        //     &:focus {text-decoration-line: underline;}
        //   }
        // }
      }

      blockquote {
        margin: 0;
        
        @include bp-m {display: flex;}

        div.quote {
          position: relative;
          border-radius: $border-radius;
          padding: $spacing;
          background: darken($clr-b2,5%);
          text-align: center;

          @include bp-m {
            flex: 1 1 auto;
            order: 2;
          }

          &.size-small {font-size: $fs-18;}
          &.size-medium {font-size: $fs-24; font-weight: 300;}
          &.size-large {font-size: $fs-25; font-weight: 300; @include bp-m { font-weight: 200; font-size: $fs-35;}}

          &:after {
            position: absolute;
            top: 100%; left: 50%;
            border: 10px solid;
            border-color: darken($clr-b2,5%) transparent transparent darken($clr-b2,5%);
            content: "";

            @include bp-m {
              top: auto; left: auto; bottom: 0.7rem; right: 100%;
              margin-left: -10px;
              border-color: darken($clr-b2,5%) darken($clr-b2,5%) transparent transparent;
            }
          }

          >:last-child {margin-bottom: 0;}

/*           p {
            &:before {content: "\0201C";}
            &:after {content: "\0201D";}
          } */
        }

        div.source {
          margin-top: calc($vmargin/2);

          @include bp-m {
            flex: 0 0 auto;
            display: flex;
            align-items: flex-end;
            margin: 0;
            //width: 16rem;

            &.download {width: 18rem;}
          }

/*           span.initials {
            display: inline-block;
            vertical-align: middle;
            margin: 0.75rem 0.75rem 0;
            border-radius: 3rem;
            width: 2.75rem;
            height: 2.75rem;
            line-height: 2.75rem;
            background: $clr-b1a;
            text-decoration: none;
            text-align: center;
            font-size: $fs-22;
            font-weight: 300;
            color: white;

            @include bp-m {
              flex: 0 0 auto;
              order: 2;
              margin: 0 1.4rem 0 0.75rem;
            }
          } */

          p {
            vertical-align: middle;
            margin: 0.75rem 0 0;
            text-align: center;
            font-size: $fs-16;

            @include bp-s {
              display: inline-block;
            }

            @include bp-m {
              flex: 1;
              margin-right: 2rem;
              text-align: right;
            }

            span {display: block;}

            strong {
              display: block;
              font-size: $fs-22;
            }
          }
        }
      }
    }
  }

  &.newsletter {
    text-align: center;

    div.wrap {
      display: block;

      > h2 {
        font-size: $fs-30;
        font-weight: 800;

        + p {margin-bottom: 0.75rem;}
      }

      > p {
        margin: 0;
      }
    }

    p.privacy-notice a {
      color: $clr-b5;

      @include bp-m {
        &:hover,
        &:focus {
          color: $clr-b1;
        }
      }
    }
  }

  &.multi-testimonial {
    div.wrap {
      display: block;

      ul.row {
        margin-bottom: 0;

        @include bp-m {
          margin-top: -3rem;
          li {display: flex;}
        }

        li.col {
          @include bp-m-less {
            margin-bottom: $vmargin;
            &:last-child {margin-bottom: 0;}
          }
          @include bp-m {
            flex: 1;
          }
        }
      }

      blockquote {
        margin: 0;

        @include bp-m {
          display: flex;
          flex-direction: column;
        }

        div.quote {
          position: relative;
          border-radius: $border-radius;
          padding: $spacing;
          background: darken($clr-b2,5%);
          text-align: center;
          text-wrap: pretty;
          box-shadow: 0 0 20px 5px rgba(black,0.10);

          &.size-small {font-size: $fs-18;}
          &.size-medium {font-size: $fs-24; font-weight: 300;}
          &.size-large {font-size: $fs-25; font-weight: 300; @include bp-m { font-weight: 200; font-size: $fs-35;}}

          @include bp-m {
            flex: 1 1 auto;
            display: flex;
            align-items: center;
          }

          &:after {
            position: absolute;
            top: 100%; left: 50%;
            border: 10px solid;
            border-color: darken($clr-b2,5%) transparent transparent darken($clr-b2,5%);
            content: "";
          }

          >:last-child {margin-bottom: 0;}

/*           p {
            &:before {content: "\0201C";}
            &:after {content: "\0201D";}
          } */
        }

        div.source {
