      margin-bottom: inherit;
    }
  }
  ol {@extend .decimal;}

  figure {
    overflow: hidden;
    border-radius: $border-radius;
    margin: 0 auto $vmargin;
    max-width: $article-width;

    &.border {border: 6px solid $clr-4;}
  }
}

// general wrap for lists

div.general-wrap {
  overflow: hidden;
  margin-bottom: $vmargin;
  border-radius: $border-radius;

  &.spacer-up + p {@extend .spacer-up;}

  > ol,
  > ul {
    margin-bottom: 0;

    > li {
      margin-top: 4px;
      &:nth-child(1) {margin-top: 0;}
    }
  }
}

// announcement

div.announcement {
  padding: $spacing;
  background: $clr-warning;
  text-align: center;
  color: $clr-1;

  div.page-intro & {
    margin-bottom: $vmargin;
    border-radius: $border-radius;
  }

  div.home-intro & {
    margin-top: $vmargin;
    font-size: $fs-25;

    a {font-weight: 700;}
  }

  //div.wrap > :last-child {margin-bottom: 0;}

  span.text {
    @include bp-m {display: inline-block;}

    > span {
      display: block;
      margin: calc($vmargin/3) 0;

      @include bp-m {
        display: inline-block;
        //margin-bottom: 0;
      }
    }

    .button {
      margin: calc($vmargin/3) 0;
      @include bp-m {margin-left: calc($spacing/2);}
    }
  }
}

// slideout modal

$modalWidth: 650px;

div.slideout-modal {
  display: block;
  position: fixed;
  top: 0; bottom: 0; left: 100%;
  z-index: 1000;
  background: rgba(black, 0.3);
  text-align: center;

  &:target {
    left: 0; right: 0;
    p.close a:before {right: 0.75rem; left: auto; opacity: 1;}
    div.content {right: 0;}
  }

  div.content {
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    position: absolute;
    top: 0; bottom: 0; right: (-$modalWidth);
    z-index: 2;
    margin: 0 auto; 
    padding: 1.75rem;
    width: 100%;
    background: $clr-b2;
    color: white;
    transition: right 0.2s;

    @include bp-m {width: $modalWidth;}  

    //> p {margin-bottom: 5rem;}
  }

  p.close {
    margin-bottom: -$vmargin;
    font-size: $fs-17;

    a {
      text-decoration: none;

      &:before {
        position: absolute;
        top: 0.75rem; left: 100%;
        z-index: 3;
        border-radius: 3rem;
        padding: 0;
        width: 2rem;
        height: 2rem;
        line-height: 2.1rem;
        background: $clr-b5;
        font-size: 1.2rem;
        color: $clr-b2;
        opacity: 0;
        transition: opacity 0.2s 0.2s;
      }

      &:hover:before,
      &:focus:before {
        background: lighten($clr-b5,7%);
        color: $clr-b2;
      }

      &:active:before {
        background: darken($clr-b5,7%);
        box-shadow: inset 0 0 2px 2px rgba(black,0.2);
      }

      &:after {
        position: absolute;
        top: 0; left: 0; right: 0; bottom: 0;
        z-index: 1;
        content: "";
        cursor: default;
      }
    
      span {@include hide-screen();}
    }
  }
}

// modal

div.modal {
  display: block;
  position: fixed;
  top: 0; left: -100em; bottom: 0;
  z-index: 999;
  padding-top: ($gutter*2);
  background: black;
  text-align: center;

  @include bp-s {
    padding: ($gutter*2) $gutter $gutter;
    background: rgba(black, 0.8);
  }  

  @include bp-m {padding: ($gutter*2);}  

  &:target {
    left: 0; right: 0;

    @include bp-s {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  div.iframe-container {
    position: absolute;
    overflow: hidden;
    margin: 0 auto;
    padding: 0 $gutter;
    max-width: 820px;
    width: 100%;
    background: #f7f7f7;

    @include bp-s-less {top: 2.75rem; bottom: 0; left: 0; right: 0;}

    @include bp-s {
      position: relative;
      border-radius: $border-radius;
    }

    iframe {
      position: absolute;
      top: 0; bottom: 0; left: 0; right: 0;
      display: block;
      z-index: 2;
      width: 100%;
      height: 100%;

      @include bp-s {
        position: relative;
        margin-bottom: -35px; // to hide the Pipedrive link
        height: 735px;
      }
    }
  }

  div.content {
    position: absolute;
    padding: $gutter;
    background: white;

    @include bp-s-less {top: 2.75rem; left: 0; right: 0; bottom: 0;}

    @include bp-s {
      position: relative;
      margin: 0 auto;
      border-radius: $border-radius;
      max-width: 540px;
    }

    div.text {
      position: relative;
      z-index: 2;
      font-size: $fs-18;

      >:last-child {margin-bottom: 0;}
    }
  }

  p.close {
    margin-bottom: -$vmargin;
    font-size: $fs-17;

    a {
      text-decoration: none;

      span {
        display: inline-block;
        position: relative;
        top: -2rem;
        z-index: 3;
        border-radius: 2rem;
        padding: 0.3rem 0.6rem;
        background: rgba(white,0.15);
        color: white;

        @include bp-m {
          &:hover,
          &:focus {
            background: $clr-b1;
            color: white;
            cursor: pointer;
          }
        }
      }

      @include bp-s {
        &:before {
          position: absolute;
          top: 0; left: 0; right: 0; bottom: 0;
          z-index: 1;
          content: "";
          cursor: default;
        }
      }
    }
  }
}

// promotions

div.promotion {
  padding: calc($spacing/1.5) 0;
  background: $clr-b1;
  color: white;

  @include bp-m {text-align: center;}

  div.wrap > :last-child {margin-bottom: 0;}

