// notices

p.notice {
  position: relative;
  border: 3px solid $clr-4;
  border-radius: $border-radius;
  padding: 1rem;
  background: white;
  text-align: left!important;
  color: $clr-1;

  &:before {
    position: absolute;
    left: 0.8rem; top: 0.95em;
    font-size: $fs-24;
  }

  &.error,
  &.success,
  &.warning,
  &.caution,
  &.info {padding-left: 2.5rem;}

  &.error {
    border-color: $clr-error;
    background: white;

    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
      color: $clr-error;
    }
  }

  &.success {
    border-color: $clr-success;

    &:before {
      @extend .icon-extension,.icon-check-circle;
      color: $clr-success;
    }
  }

  &.offer {
    border-color: $clr-b1;
    background: rgba($clr-b1,0.05);
    text-align: center;

    &:before {content: none;}

    div.buy-details & {
      border-color: white;
      background: rgba(white,0.1);

      span {
        display: block;
        font-size: $fs-18;
      }

      b {font-size: $fs-22;}
    }
  }

  &.warning {
    border-color: $clr-warning;

    &:before {
      @extend .icon-extension,.icon-exclamation-triangle;
    }
  }

  &.caution {
    &:before {
      @extend .icon-extension,.icon-exclamation-circle;
    }
  }

  &.info {
    &:before {
      @extend .icon-extension,.icon-info-circle;
    }
  }
}

// guide

div.guide-layout {
  padding: calc($spacing*5) 0 $spacing;
  @include bp-m {padding-top: calc($spacing*6);}

  // guide main col

  div.guide-main {
    @include bp-m {
      order: 2;
    }

    article {
      font-size: 0.9rem;

      ul {@extend .disc;}
      ol {@extend .decimal;}

      > h1,
      > p.lead {
        text-wrap: balance;
        text-align: center;
        @include bp-m {text-align: left;}
      }
    }

    h1 {
      font-size: $fs-30;
      @include bp-m {font-size: $fs-40;}
    }

    h2 {
      font-size: $fs-25;
      @include bp-m {font-size: $fs-30;}
    }

    h3 {
      font-size: $fs-22;
      @include bp-m {font-size: $fs-25;}
    }

    div.meta {
      display: flex;
      align-items: flex-end;
      margin-bottom: $vmargin;
      border-bottom: 5px solid $clr-3;
      padding-bottom: 0.7rem;

      ul.general {
        flex: 1;
        margin: 0;
        font-size: $fs-16;

        li {
          display: flex;
          align-items: center;

          &:before {
            margin-right: 0.2rem;
            font-size: $fs-18;
            color: $clr-b2;
          }
        }
      }

      ul.share-options {
        flex: 0 1 auto;
        margin: 0;
      }
    }
  }

  // guide side col

  div.guide-side {
    div.content {

      @include bp-m {
        border-radius: $border-radius;
        padding: $spacing;
        background: linear-gradient(to bottom, rgba(white,0.5) 0%, $clr-4 100%);
      }
    }

    div.promo {
      a {
        display: block;
        margin-bottom: $vmargin;
        border-radius: $border-radius;
        padding: 1rem;
        text-align: center;
        font-weight: 400;
        font-size: $fs-16;
        color: white;
        text-decoration: none;
        transition: background 0.2s;

        @include bp-m {
          &:hover,
          &:focus {
            background-color: lighten($clr-b2,2%);
            background-size: 120px auto;
          }
        }

        span.h5 {
          display: inline-block;
          margin-bottom: 0.5rem;
          font-size: $fs-23;
          line-height: 1.2;
          span {display: block;}
        }

        span.text {
          display: block;
          margin-bottom: 0.75rem;
        }
      }
    }

    h2 {
      margin: 0 0 0.75rem;
      border-radius: 0.25rem;
      padding: 0.4rem;
      padding-left: $spacing;
      background: $clr-3;
      font-size: $fs-15;
      font-weight: 800;
      color: $clr-b2;
      text-transform: uppercase;

      @include bp-m {
        margin: 0 0 0.75rem (-$spacing);
        border-radius: 0 0.25rem 0.25rem 0;
      }
    }

    div.guide-topics {

      a {
        font-weight: 400;
        text-decoration: none;
      }

      > ul {
        margin-bottom: 0.75rem;
        font-size: $fs-16;
        box-shadow: inset 4px 0 0 $clr-b0;

        li {

          a {
            display: block;
            position: relative;
            border-radius: 0 0.1rem 0.1rem 0;
            padding: 0.05rem 0.5rem 0.05rem 1.2rem;

            &:before {
              position: absolute;
              left: 0; top: 0.5rem;
              width: 10px; height: 4px;
              background: $clr-b0;
              content: "";
            }

            @include bp-m {
              &:hover,
              &:focus {
                background: $clr-3;
                box-shadow: inset 4px 0 0 $clr-b1;
                &:before {background: $clr-b1;}
              }
            }

            &.current {
              box-shadow: inset 4px 0 0 $clr-b1;
              color: $clr-b1;
              
              &:before {
                background: $clr-b1;
              }
            }
          }
        }

        ul {
          margin: 0;

          li a {
            padding-left: 1.75rem;
          }
        }
      }
    }

    div.related-resources {
      a {
        font-weight: 400;
        text-decoration: none;
      }

      ul.icons {
        font-size: $fs-16;

        @include bp-m {margin: 0;}

        li {
          &:before {color: $clr-b2;}

          a {
            text-decoration-line: none;

            @include bp-m {
              &:hover,
              &:focus {text-decoration-line: underline;}
            }
          }
        }
      }
    }
  }
}

