// fieldset

fieldset > div:first-of-type {margin-top: 0.25rem;}

fieldset.horizontal-groups {
  @include bp-l {
    display: flex;
    justify-content: center;
  }

  div.form-group {
    margin: 0.5rem 0.25rem;
  }

  label {
    display: block;
    margin: 0;

    //&:first-of-type {margin-top: 0;}

    @include bp-l {
      flex: 1;
/*       margin: 0 0.25rem;
      &:first-of-type {margin-left: 0;}
      &:last-of-type {margin-right: 0;} */
    }

    input {width: 100%;}
  }
}

// form group structure

div.form-group {
  position: relative;
  margin: calc($vmargin/1.5) 0;

  footer.footer & {margin: calc($vmargin/3) 0;}

  &.newsletter-request {
    text-align: left;
    span.text {font-size: $fs-18;}
  }

  span.label,
  p.label {
    display: block;
    margin-bottom: 0.25rem;
    font-weight: 400;
  }

  span.field {
    position: relative;
    display: block;
    margin: 0;

    &:before { // icons
      @extend .icon-extension;
      position: absolute;
      top: 0; right: 0.75rem;
      font-size: $fs-16;
      line-height: 2.8;
      z-index: 2;
    }

    input:not([type=checkbox]):not([type=radio]):not([type=submit]),
    textarea,
    select {
      width: 100%;
      
      @include bp-m {
        &.size-small {width: $ff-s;}
        &.size-medium {width: $ff-m;}
        &.size-large {width: $ff-l;}
      }
    } 

    // connected field button

    &.field-button {
      position: relative;
      z-index: 1;
      display: flex;
      margin: 0 auto;
      max-width: 500px;

      label {
        flex: 1 1 auto;

        input {
          position: relative;
          z-index: 2;
          border-radius: 0.25rem 0 0 0.25rem!important;
          border-right-color: transparent!important;
          width: 100%!important;

          &:focus {
            box-shadow: none!important;
            + span.active {display: block;}   
          }
        }

        // this is the pseudo active shadow
        span.active {
          display: none;
          position: absolute;
          top: 0; left: 0; bottom: 0; right: 0;
          z-index: 1;
          border-radius: 0.25rem;
          box-shadow: 0 0 0 5px rgba($clr-fields,0.25);
        }
      }

      button {
        position: relative;
        z-index: 2;
        flex: 0 0 auto;
        border-radius: 0 0.25rem 0.25rem 0;
        padding: 0.25em 0.75em;
        font-size: $fs-18;
        /* box-shadow: inset 0 -2px 1px rgba(black,0.2); */

/*         @include bp-m {
          &:hover,
          &:focus {
            background: lighten($clr-b1, 5%)!important;
            box-shadow: none!important;
          }

          &:active {
            background: $clr-b1!important;
            box-shadow: inset 0 1px 2px rgba(black,0.75)!important;
            transition: box-shadow 0.25s;
          }
        } */
      }
    }
  }

  &.linear-fields {
    @include bp-l {display: flex;}

    label {
      display: block;
      margin: calc($vmargin/1.5) 0;

      &:first-of-type {margin-top: 0;}

      @include bp-l {
        flex: 1;
        margin: 0 0.25rem;
        &:first-of-type {margin-left: 0;}
        &:last-of-type {margin-right: 0;}
      }

      input {width: 100%;}
    }
  }
}

// form elements

input,
textarea,
select,
button {
  font-family: $ff-1;
  font-size: $fs-18;
  line-height: $lh-general;
}

input:not([type=checkbox]):not([type=radio]):not([type=submit]):not([type=file]),
textarea,
.custom-select {
  border: 1px solid $clr-fields;
  border-radius: 0.25rem;
  padding: 0.6rem 0.75rem;
  background: white;
  color: $clr-1;
  box-sizing: border-box;
  box-shadow: none;
  outline: none;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;

  &:focus {
    box-shadow: 0 0 0 5px rgba($clr-fields,0.25);
    outline: none;
  }

  //footer.footer div.newsletter & {padding: 0.45rem 0.75rem;}
}

textarea {
  display: block;
  width: 100%;
  //height: 9.25rem;
}

::-webkit-input-placeholder {color: $clr-placeholder;}
::-moz-placeholder {color: $clr-placeholder; opacity: 1;}
:-ms-input-placeholder {color: $clr-placeholder!important;}
::-ms-input-placeholder {color: $clr-placeholder!important;}
::placeholder {color: $clr-placeholder;}

