// custom radio & checkbox buttons

label.custom-control {
  position: relative; // required for HTML error message position
  display: flex;
  align-items: center;
  cursor: pointer;

  span {
    position: relative;
    flex: 0 0 auto;
    content: "";

    &.input {margin-right: 0.5rem;}

    &.text {
      display: block;
      flex: 0 1 auto;

      @include bp-l {
        &:hover {color: $clr-b1;}
      }
    }
    
    // radio

    &.radio {
      display: block;
      border: 1px solid $clr-fields;
      border-radius: 50%;
      width: 25px;
      height: 25px;
      background: white;

      &:before {
        position: absolute;
        top: 5px; left: 5px;
        border-radius: 50%;
        width: 13px;
        height: 13px;
        background: $clr-b1;
      }
    }
    
    // checkbox

    &.checkbox {
      display: block;
      border: 1px solid $clr-fields;
      border-radius: 2px;
      width: 23px;
      height: 23px;
      background: white;

      &:before {
        @extend .icon-extension;
        position: absolute;
        top: 3px; left: 3px;
        font-size: $fs-16;
        color: $clr-b1;
        content: none;
      }
    }
  }

  input {
    @include hide-input(); // I changed to this and haven't tested it
    position: absolute;
    top: 6px; left: 6px; // required for HTML error message position

    &:checked {
      ~ span.radio:before {content: "";}
      ~ span.checkbox:before {@extend .icon-checkbox;}
      ~ span.text {color: $clr-b1;}
    }

    &:focus {
      ~ span.radio,
      ~ span.checkbox {box-shadow: 0 0 0 5px rgba($clr-fields,0.25);}
    }
  }
}

// legend

p.legend {
  position: relative;
  margin-bottom: 0;
  text-align: center;
  font-size: $fs-17;
  font-weight: 700;

  span {
    position: relative;
    z-index: 2;
    display: inline-block;
    border-radius: 0.25rem 0.25rem 0 0;
    padding: 0.25rem 0.75rem;
    background: rgba($clr-1,0.075);
  }
}

// inputs

.inputs {
  margin: 0.75rem 0;
  font-size: $fs-17;

  li {margin: 0.25rem 0;}
}

// options select 

.options-select {
  position: relative; // required for error message position
  margin: 0;
  border-radius: 0.25rem;
  padding: 0.75rem;
  background: rgba($clr-1,0.075);

  @include bp-l {display: flex;}

  li {
    margin: 0.5rem 0;

    @include bp-l {
      flex: 1;
      margin: 0 0.25rem;
      &:first-child {margin-left: 0;}
      &:last-child {margin-right: 0;}
    }

    label {
      display: block;
      cursor: pointer;

      input {  
        position: absolute;
        z-index: -1;
        bottom: 0; left: 49%; // required for HTML error message position

        &:checked,
        &:active {
          ~ span.button {
            background: $clr-b6!important;
            color: white!important;
            box-shadow: inset 0 1px 2px rgba(black,0.4)!important;
            cursor: default;

            &:before {
              @extend .icon-extension, .icon-check-circle;
              position: absolute;
              top: 10px; left: 10px;
              font-size: $fs-25;
              color: white;
            }
          }
        }
      }

      span.button {
        position: relative;
        display: block;
        padding: 1rem;
        font-size: $fs-20;

        span {display: block;}
      }
    }
  }
}

// reveal fields - mdr support call options - don't think this is used live

div.reveal-fields {
  input#reveal-fields {
    @include hide-input();

    &:checked {
      + p.cta {
        display: none;
        + div.form-group {display: block;}
      }
    }
  }

  p.cta {
    font-size: $fs-16;
    label.anchor {color: white;}
  }

  div.form-group {display: none;}
}
