footer.footer {
  background: darken($clr-b2,5%);
  font-size: $fs-16;
  color: white;

  @include bp-m {font-size: $fs-14;}

  body.analytics & {display: none;}

  a:not(.button) {
    color: white;
    text-decoration-line: none;

    @include bp-m {
      &:hover,
      &:focus {
        text-decoration-line: underline;
        color: $clr-b5;
      }
    }
  }

  // site

  div.site {
    padding: calc($spacing*2) 0;

    div.links {
      overflow: hidden;
      margin: 0 0 $vmargin;
      border-radius: $border-radius;

      @include bp-s {
        display: flex;
        flex-wrap: wrap;
      }

      @include bp-m {
        order: 2;
        flex-wrap: nowrap;
        margin: 0 (-$gutter);
      }

      div.area {
        padding-bottom: 4px;
        &:last-child {padding-bottom: 0;}

        @include bp-s {
          display: flex;
          align-items: stretch;
          flex: 0 1 auto;
          width: 50%;

          &:nth-of-type(1) {padding-right: 4px;}
          &:nth-of-type(3) {padding-right: 4px; padding-bottom: 0;}
        }

        @include bp-m {
          flex: 1 1 auto;
          padding: 0 calc($gutter/2);
          width: auto;

          &:nth-of-type(odd) {padding: 0 calc($gutter/2) 0 $gutter;}
          &:nth-of-type(even) {padding: 0 $gutter 0 calc($gutter/2);}

          &:first-of-type {padding-left: $gutter;}
          &:last-of-type {padding-right: $gutter;}
        }

        div.content {
          padding: $spacing;
          background: rgba(black,0.1);

          @include bp-s {flex: 1;}

          @include bp-m {
            padding: 0;
            background: none;
          }
        }

        h3 {
          margin: 0;
          // border-bottom: 2px solid rgba(white,0.1);
          // padding-bottom: 0.4rem;
          font-size: $fs-18;
          font-weight: 700;
          color: $clr-b5;

          @include bp-m {
            //border: 0;
            //padding: 0;
          }

          a {color: $clr-b5;}
        }

        ul {
          margin-bottom: 0;
          text-align: left;

          @include bp-s {margin-bottom: 0;}

          li {
            margin: 0.7rem 0 0 0;
            line-height: 1.2;

            a {
              font-weight: 400;
              &:before {content: none;}
              b {font-weight: 400;}
              span.snippet {@include hide-screen();}
            }
          }
        }
      }
    }

    // company

    div.company {
      text-align: center;
      font-size: $fs-14;

      @include bp-m {
        &.col {text-align: left;}

        body.landing & {margin-top: 0;}

        > div.col:first-child {text-align: left;}
        > div.col:last-child {text-align: right;}
      }

      h2 {
        display: inline-block;
        margin: 0 0 0.5rem;
        width: 100%;
        max-width: 265px;
        background: url(../../assets/images/logos/inverted-mantra-systems.svg) no-repeat 50% 100%; 
        background-size: 100% auto;

        &:before {
          display: block;
          padding-top: 12%;
          content: "";
        }

        span {@include hide-screen();}
      }

      ul.contact-buttons {
        @include bp-m {margin-bottom: calc($vmargin/1.5);}
        //body.landing & {@include hide-screen();}

        li {
          display: inline-block;
          margin: 0 0.25rem;

          @include bp-m {margin: 0 0.25rem 0 0;}

          a {
            border-radius: 0.25rem;
            padding: 0.5rem;
            background: rgba(white,0.1);
            text-decoration: none;

            &:before {
              margin: 0;
              font-size: $fs-25;
              color: white;
            }

            @include bp-m {
              &:hover,
              &:focus {background-color: rgba(white,0.2);}
            }
            
            span {@include hide-screen();}
          }
        }
      }

      p.location {
        margin: 0;
        b {display: block;}
        a {font-weight: 400;}
      }

      p.info {margin: 0;}


      // logos

      ul.registration-logos {
        margin: 1rem 0 0;

        @include bp-s {
          display: flex;
          justify-content: center;
        }

        @include bp-m {
          // margin-bottom: 1rem;
          // justify-content: right;
          // justify-content: right;
        }

        li {
          display: inline-block;
          margin: 0.5rem 0;

          @include bp-s {margin: 0.5rem;}

          @include bp-m {
            margin: 0;
            &:first-child {margin-right: 1rem;}
          }

          figure {
            max-width: none;

            img {
              width: auto;
              max-width: 100%;
              max-height: 70px;
            }
          }
        }
      }
    }
  }
}
